      //
      doSearch(){
        var imageString = this.getCookie('avatarImages'); //
        this.objNr = {
          fetchPostion: this.getCookie('infoObjPassPostion'),
          fetchName: this.getCookie('infoObjPassName'),
        }
        if (imageString) {
          this.imgSrc.header = imageString;
        }
        this.$http.post('/api/v1.0/client/findPunchCardLog').then(response => { //
          if (response.body.code === 200) {
            if (response.body.result) {
              this.showOwStstus = response.body.result.twTime;
              this.showOwStstusX = response.body.result.twTime; //
              this.daAfter = response.body.result.owTime;
              if (response.body.result.owTime) {
                this.showOwStstus = false;
              }
              //start
              this.searchLocationArray = response.body.result.locations;
              //end
              //start
              this.twRangeShow = response.body.result.twLocation;
              this.owRangeShow = response.body.result.owLocation;
              //end
              //falsestart

              this.fanwei = response.body.result.twOutside;
              this.fanweixia = response.body.result.owOutside;
              this.toDaKaStatusIsInit = response.body.result.twStatus; //
              this.toDaKaStatusIsOutsideInit = response.body.result.twOutside; //
              this.toDownKaStatusIsInit = response.body.result.owStatus;//
              this.toDownKaStatusIsOutsideInit = response.body.result.owOutside; //
              if (response.body.result.overTime !== null) {
                this.$nextTick(() => {
                  this.addtime = response.body.result.overTime;
                  this.setCookie('upAddTime', this.addtime, 365);
                });
              }
              if (response.body.result.twTime) {
                this.toClickSpan = false;
                this.downClickSpan = true;
              }
              if (response.body.result.twTime) {
                this.initToTime = moment(response.body.result.twTime).format(df);
              }
              if (response.body.result.owTime) {
                this.goToTime = moment(response.body.result.owTime).format(df);
              }
              //
              if (this.toDaKaStatusIsInit == 0) {//
                this.alertToSpan = false;//sapn 
                this.alertDownSpan = false; //sapn 
                this.initDaKaRecord = true;
                this.lateStatus = false;
                this.lateStatusAddW = false;
                this.absenteeismStatus = false;//
                this.initDaKaRecordWeiZhi = true; //6-12-15bug
              }
              if (this.toDaKaStatusIsInit == 1 && this.toDaKaStatusIsOutsideInit) { //+
                this.initDaKaRecord = false;
                this.lateStatusAddW = true;
                this.tokuangWdk = false; //
                this.lateStatus = false; //
                this.lateStatusTo = true; //
                this.$nextTick(() => {
                  this.absenteeismStatus = false; //
                  this.absenteeismStatus2 = true; //
                });
                this.initDaKaRecordWeiZhi = true; //6-12-15bug
              } else if (this.toDaKaStatusIsInit == 1) { //
                this.lateStatus = true;
                this.initDaKaRecord = false;
                this.isYellow = false;
                this.absenteeismStatus = false;
                this.initDaKaRecordWeiZhi = true;
              }
              if (this.toDaKaStatusIsInit == 2) { //
                this.absenteeismStatus = true;
                this.initDaKaRecord = false;
                this.lateStatus = false;
                this.tokuangWdk = true;
                this.initDaKaRecordWeiZhi = true;
              }
              if (this.toDaKaStatusIsOutsideInit) { //
                this.lateStatusAddW = true;
                this.initDaKaRecord = response.body.result.twStatus == 0 ? true : false;
                this.initDaKaRecordWeiZhi = true;
                this.absenteeismStatus = true;
              }
              if (this.toDaKaStatusIsOutsideInit && this.toDaKaStatusIsInit == 0) { //+
                this.$nextTick(() => {
                  this.lateStatusAddW = true;
                  this.initDaKaRecord = response.body.result.twStatus == 0 ? true : false;
                  this.initDaKaRecordWeiZhi = true;
                  this.absenteeismStatus = false; //span
                  this.absenteeismStatus2 = true;
                  this.tokuangWdk = false;
                  this.lateStatus = false;
                });
              }

//   ******************************************


              if (this.toDownKaStatusIsInit == 0 || this.toDownKaStatusIsInit == 1 || this.toDownKaStatusIsInit == 2 || this.toDownKaStatusIsInit == 3) {     //****
                if (this.toDownKaStatusIsInit == 0) {//
                  console.log('toDaKaStatusIsInit' + this.toDaKaStatusIsInit)
                  this.zcDownShowSpan = true; //
                  this.initDownRecord = true;
                }
                if (this.toDownKaStatusIsInit == 0 && this.toDownKaStatusIsOutsideInit) {//+
                  console.log('toDaKaStatusIsInit' + this.toDaKaStatusIsInit)
                  this.zcDownShowSpan = true; //
                  this.initDownRecord = true;
                  this.toDownLateStatusAddW = true;
                }

                if (this.toDownKaStatusIsOutsideInit) { //
                  this.$nextTick(() => {
                    this.toDownLateStatusAddW = true;
                    this.toDownAbsenteeismStatus = true;
                  });
                }
                if (this.toDownKaStatusIsInit == 1 && this.toDownKaStatusIsOutsideInit) { //+
                  this.$nextTick(() => {
                    this.zcDownShowSpan = true; //
                    this.toDownAbsenteeismStatus = true; //span
                    this.toDownLateStatus = false; //span 
                    this.leaveEarly = true; //
                    this.toDownLateStatusAddW = true; //
                  });
                } else if (this.toDownKaStatusIsInit == 1) { //
                  this.zcDownShowSpan = true; //
                  this.leaveEarly = true; //
                  this.toDownAbsenteeismStatus = true;
                }
                if (this.toDownKaStatusIsInit == 2) { //
                  this.zcDownShowSpan = true;

                  this.toDownAddTimeStatus = true;
                  console.log('8' + this.toDaKaStatusIsInit)
                }
                if (this.toDownKaStatusIsInit == 2 && this.toDownKaStatusIsOutsideInit) { //+
                  this.$nextTick(() => {
                    this.toDownLateStatusAddW = false;
                    this.toDownAbsenteeismStatus = false; //span
                    this.toDownAddTimeStatus = true;
                    this.toDownLateStatusLeft = true;  //
                  });
                }
                if (this.toDownKaStatusIsInit == 3) { //
                  this.toDownAbsenteeismStatus = false; //span
                  this.toDownKuang = true;
                  this.zcDownShowSpan = true;
                  this.toDownAbsenteeismStatus = true;
                }
                if (this.toDownKaStatusIsInit == 3 && this.toDownKaStatusIsOutsideInit) { //+
//              alert('+');
                  this.$nextTick(() => {
                    this.toDownLateStatusAddW = true;
                  });
                }
                this.daKaHide = false;  //
              }
